.table-responsive {
    display: block;
    width: 100%;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
}

.table {
    width: 100%;
    border-collapse: collapse;
    border: 0;
    vertical-align: top;

    &-auto {
        width: auto;
    }

    >:not(caption)>* {
        >* {
            padding: 1rem 1rem;
            border-bottom: 1px solid $color-gray-40;
            box-shadow: inset 0 0 0 9999px transparent;

            @media (min-width: 1056px) {
                padding: 0.5rem 0.75rem;
            }
        }
    }

    &-pdg-sm {
        >:not(caption)>* {
            >* {
                padding: 0.5rem 1rem;
            }
        }
    }

    &-border-left {
        border-left: 1px solid $color-gray-40;
    }

    &-border-right {
        border-right: 1px solid $color-gray-40;
    }

    &-border-round {
        border: 1px solid $color-gray-40;
        border-spacing: 0;
        border-collapse: separate;
        border-radius: 5px;
        overflow: hidden;
    }

    thead {
        vertical-align: middle;

        th,
        td {
            border-color: $color-gray-40;
            font-weight: 500;
            background-color: $color-gray-20;
            color: $color-gray-70;
            text-align: left;
            font-size: 14px;
            line-height: 1.3;

            @media (min-width: 1056px) {
                font-size: 12px;
            }

            @media (max-width: 1055.98px) {
                border-top: 1px solid $color-gray-40;
            }
        }

        .th-bg-bold {
            background-color: $color-gray-40;
        }
    }

    >tbody {
        tr {
            &:hover {
                >* {
                    background-color: $color-gray-10;
                }
            }
        }
    }

    tbody {

        th,
        td {
            font-weight: 400;
            background-color: $color-white;
            color: $color-gray-70;
            font-size: 14px;
            line-height: 1.3;

            @media (min-width: 1056px) {
                font-size: 12px;
            }
        }

        >* {
            &:last-child {
                >* {
                    @media (min-width: 1056px) {
                        border-bottom: 0;
                    }
                }
            }
        }
    }

    tfoot {

        th,
        td {
            font-weight: 400;
            background-color: $color-white;
            font-size: 14px;
            line-height: 1.3;

            @media (min-width: 1056px) {
                font-size: 12px;
                border-top: 1px solid $color-gray-40;
                border-bottom-width: 0;
            }

            @media (max-width: 1055.98px) {
                border-bottom: 1px solid $color-gray-40;
            }
        }
    }
}